<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item"></span>
  </div>
</template>

<script type="text/ecmascript-6">
  const LENGTH = 5;
  const CLS_ON = 'on';
  const CLS_HALF = 'half';
  const CLS_OFF = 'off';

  export default {
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      // 通过计算属性，从父组件传入star的大小，方便css样式的编写，提高复用性
      starType() {
        return 'star-' + this.size;
      },
      // 需要遍历的星星的数组处理
      itemClasses() {
        let result = [];
        // 评分向下取整（0.5倍数）-> 全星／半星／空星
        let score = Math.floor(this.score * 2) / 2;
        // 是否有小数 x.5
        let hasDecimal = score % 1 !== 0;
        let integer = Math.floor(score);
        // 先向result中push进所需的全星数量
        for (let i = 0; i < integer; i++) {
          result.push(CLS_ON);
        }
        // 如果有小数，再向result中push进一个半星
        if (hasDecimal) {
          result.push(CLS_HALF);
        }
        // 判断长度是否超过所需星星的长度，未到再push进剩余数量的空星
        while (result.length < LENGTH) {
          result.push(CLS_OFF);
        }
        return result;
      }
    }
  }
</script>

<style lang="stylus" type="text/stylus">
  @import "../../common/stylus/mixin.styl"

  .star
    font-size: 0
    .star-item
      display: inline-block
      background-repeat: no-repeat
    &.star-48
      .star-item
        width: 20px
        height: 20px
        margin-right: 22px
        background-size: 20px 20px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star48_on')
        &.half
          bg-image('star48_half')
        &.off
          bg-image('star48_off')
    &.star-36
      .star-item
        width: 15px
        height: 15px
        margin-right: 6px
        background-size: 15px 15px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star36_on')
        &.half
          bg-image('star36_half')
        &.off
          bg-image('star36_off')
    &.star-24
      .star-item
        width: 10px
        height: 10px
        margin-right: 3px
        background-size: 10px 10px
        &:last-child
          margin-right: 0
        &.on
          bg-image('star24_on')
        &.half
          bg-image('star24_half')
        &.off
          bg-image('star24_off')
</style>



